  color: #969799;
  font-size: 12px;
  line-height: 16px;
}
.van-sku__price-symbol {
  font-size: 16px;
  vertical-align: bottom;
}
.van-sku__price-num {
  font-weight: 500;
  font-size: 22px;
  vertical-align: bottom;
  word-wrap: break-word;
}
.van-sku__goods-price {
  color: #ee0a24;
}
.van-sku__price-tag {
  position: relative;
  display: inline-block;
  margin-left: 8px;
  padding: 0 5px;
  overflow: hidden;
  color: #ee0a24;
  font-size: 12px;
  line-height: 16px;
  border-radius: 8px;
}
.van-sku__price-tag::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: currentColor;
  opacity: 0.1;
  content: '';
}
.van-sku-group-container {
  padding-top: 12px;
}
.van-sku-group-container--hide-soldout .van-sku-row__item--disabled {
  display: none;
}
.van-sku-row {
  margin: 0 16px 12px;
}
.van-sku-row:last-child {
  margin-bottom: 0;
}
.van-sku-row__title {
  padding-bottom: 12px;
}
.van-sku-row__title-multiple {
  color: #969799;
}
.van-sku-row__item {
  position: relative;
  display: -webkit-inline-box;
  display: -webkit-inline-flex;
  display: inline-flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  min-width: 40px;
  margin: 0 12px 12px 0;
  overflow: hidden;
  color: #323233;
  font-size: 13px;
  line-height: 16px;
  vertical-align: middle;
  border-radius: 4px;
  cursor: pointer;
}
.van-sku-row__item::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #f7f8fa;
  content: '';
}
.van-sku-row__item-img {
  z-index: 1;
  width: 24px;
  height: 24px;
  margin: 4px 0 4px 4px;
  object-fit: cover;
  border-radius: 2px;
}
.van-sku-row__item-name {
  z-index: 1;
  padding: 8px;
}
.van-sku-row__item--active {
  color: #ee0a24;
}
.van-sku-row__item--active::before {
  background: currentColor;
  opacity: 0.1;
}
.van-sku-row__item--disabled {
  color: #c8c9cc;
  background: #f2f3f5;
  cursor: not-allowed;
}
.van-sku-row__item--disabled .van-sku-row__item-img {
  opacity: 0.3;
}
.van-sku-stepper-stock {
  padding: 12px 16px;
  overflow: hidden;
  line-height: 30px;
}
.van-sku__stepper {
  float: right;
  padding-left: 4px;
}
.van-sku__stepper-title {
  float: left;
}
.van-sku__stepper-quota {
  float: right;
  color: #ee0a24;
  font-size: 12px;
}
.van-sku__stock {
  display: inline-block;
  margin-right: 8px;
  color: #969799;
  font-size: 12px;
}
.van-sku__stock-num--highlight {
  color: #ee0a24;
}
.van-sku-messages {
  padding-bottom: 32px;
}
.van-sku-messages .van-cell::after {
  top: 0;
  right: 16px;
  bottom: auto;
}
.van-sku-messages__image-cell .van-cell__title {
  max-width: 90px;
}
.van-sku-messages__image-cell .van-cell__value {
  overflow: visible;
  text-align: left;
}
.van-sku-img-uploader {
  display: inline-block;
}
.van-sku-img-uploader__uploader {
  vertical-align: middle;
}
.van-sku-img-uploader__img {
  position: relative;
  float: left;
  width: 64px;
  height: 64px;
  margin-right: 8px;
  background: #f7f8fa;
  border-radius: 2px;
}
.van-sku-img-uploader__img img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.van-sku-img-uploader__delete {
  position: absolute;
  top: -12px;
  right: -14px;
  z-index: 1;
  padding: 6px;
  color: rgba(50, 50, 51, 0.8);
  opacity: 0.8;
}
.van-sku-img-uploader__delete::before {
  background-color: #fff;
  border-radius: 14px;
}
.van-sku-img-uploader__mask {
  position: absolute;
  top: 0;
  left: 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  color: white;
  background: rgba(50, 50, 51, 0.8);
}
.van-sku-img-uploader__warn-text {
  margin-top: 6px;
  font-size: 12px;
  line-height: 14px;
}
.van-sku-img-uploader__trigger {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 100%;
  height: 100%;
  color: #dcdee0;
}
.van-sku-actions {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-shrink: 0;
          flex-shrink: 0;
  padding: 8px 16px;
}
.van-sku-actions .van-button {
  height: 40px;
  font-weight: 500;
  font-size: 14px;
  border: none;
  border-radius: 0;
}
.van-sku-actions .van-button:first-of-type {
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
}
.van-sku-actions .van-button:last-of-type {
  border-top-right-radius: 20px;
  border-bottom-right-radius: 20px;
}
.van-sku-actions .van-button--warning {
  background: -webkit-linear-gradient(left, #ffd01e, #ff8917);
  background: linear-gradient(to right, #ffd01e, #ff8917);
}
.van-sku-actions .van-button--danger {
  background: -webkit-linear-gradient(left, #ff6034, #ee0a24);
  background: linear-gradient(to right, #ff6034, #ee0a24);
}
.van-slider {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: #ebedf0;
  border-radius: 999px;
  cursor: pointer;
}
.van-slider::before {
  position: absolute;
  top: -8px;
  right: 0;
  bottom: -8px;
  left: 0;
  content: '';
}
.van-slider__bar {
  position: relative;
  width: 100%;
  height: 100%;
  background-color: #1989fa;
  border-radius: inherit;
  -webkit-transition: width 0.2s;
  transition: width 0.2s;
}
.van-slider__button {
  width: 24px;
  height: 24px;
  background-color: #fff;
  border-radius: 50%;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
}
.van-slider__button-wrapper {
  position: absolute;
  top: 50%;
  right: 0;
  -webkit-transform: translate3d(50%, -50%, 0);
          transform: translate3d(50%, -50%, 0);
  cursor: grab;
}
.van-slider--disabled {
  cursor: not-allowed;
  opacity: 0.5;
}
.van-slider--disabled .van-slider__button-wrapper {
  cursor: not-allowed;
}
.van-slider--vertical {
  display: inline-block;
  width: 2px;
  height: 100%;
}
.van-slider--vertical .van-slider__button-wrapper {
  top: auto;
  right: 50%;
  bottom: 0;
  -webkit-transform: translate3d(50%, 50%, 0);
          transform: translate3d(50%, 50%, 0);
}
.van-slider--vertical::before {
  top: 0;
  right: -8px;
  bottom: 0;
  left: -8px;
}
.van-steps {
  overflow: hidden;
  background-color: #fff;
}
.van-steps--horizontal {
  padding: 10px 10px 0;
}
.van-steps--horizontal .van-steps__items {
  position: relative;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  margin: 0 0 10px;
  padding-bottom: 22px;
}
.van-steps--vertical {
  padding: 0 0 0 32px;
}
.van-swipe-cell {
  position: relative;
  overflow: hidden;
  cursor: grab;
}
.van-swipe-cell__wrapper {
  -webkit-transition-timing-function: cubic-bezier(0.18, 0.89, 0.32, 1);
          transition-timing-function: cubic-bezier(0.18, 0.89, 0.32, 1);
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
}
.van-swipe-cell__left,
.van-swipe-cell__right {
  position: absolute;
  top: 0;
  height: 100%;
}
.van-swipe-cell__left {
  left: 0;
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0);
}
.van-swipe-cell__right {
  right: 0;
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0);
}
.van-tabbar {
  z-index: 1;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  box-sizing: content-box;
  width: 100%;
  height: 50px;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
  background-color: #fff;
}
.van-tabbar--fixed {
  position: fixed;
  bottom: 0;
  left: 0;
}
.van-tabbar--unfit {
  padding-bottom: 0;
}
